import React, {useState} from 'react';


import classes from './styles/Header.module.css'

//Components import
import Title from '../../components/Title/Title'
import Logo from '../../components/Logo/Logo'
import Nav from '../../components/Nav/Nav'
import Backdrop from '../../components/UI/Backdrop/Backdrop'

function Header(props) {
    const [isClicked, setIsClicked] = useState(false)

    const burgerClickedHandler = () => {
      setIsClicked(!isClicked)
    }  

    
    return (
        <header className={classes.Header}>
            <Logo />
            <Title />
            {isClicked && <Backdrop burgerClickedHandler={burgerClickedHandler}/>}
            <Nav burgerClickedHandler={burgerClickedHandler} isClicked={isClicked}/>
            <div className={classes.menu__burger} onClick={()=>burgerClickedHandler()}>
                <div className={classes.item}></div>
                <div className={classes.item}></div>
                <div className={classes.item}></div>
            </div>
        </header>
    );
}

export default Header;