import React from 'react';

import classes from './styles/PhotoField.module.css';

import LoadingSpinner from '../UI/LoadingSpinner/LoadingSpinner'

function PhotoField({ photo, faceRect, loading }) {
  return (
    <div className={classes.photo__box}>
      {!photo && !loading ? (
        <h1 style={{color: '#273444'}}>No photo yet</h1>
      ) : loading? <LoadingSpinner />:(
        <div
          className={classes.img__box}
          style={{
            backgroundImage: `url(${photo})`,
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover'
          }}
        >
          <img
            src={photo}
            alt="DSAG"
            style={{ visibility: 'hidden', width: '100%' }}
          />
          {faceRect
            ? faceRect.map((el, i) => (
                <div
                  key={i}
                  style={{
                    position: 'absolute',
                    top: `${el.top_row*100}%`,
                    left: `${el.left_col*100}%`,
                    height: `${(el.bottom_row-el.top_row)*100}%`,
                    width: `${(el.right_col-el.left_col)*100}%`,
                    border: '2px solid red'
                  }}
                ></div>
              ))
            : null}
        </div>
      )}
    </div>
  );
}

export default PhotoField;
