import React from 'react';

import classes from './styles/InputField.module.css';

function InputField({ url, inputChangeHandler, submitHandler }) {
  return (
    <div className={classes.InputField}>
      <p>
        Put any image url below and I will try to detect faces on the picture
      </p>
      <div className={classes.InputArea}>
        <input
          type="url"
          className={classes.UrlInput}
          placeholder="example: https://..."
          value={url}
          onChange={e => inputChangeHandler(e)}
        />
        <button type="submit" onClick={(e)=>submitHandler(e)}>Send</button>
      </div>
    </div>
  );
}

export default InputField;
