import React from 'react';

import './ErrorModal.css'

function ErrorModal({error, animation}) {
    const {header, text} = error;
    return (
        <div className='ErrorModal'style={{animation: `${animation? animation: null}`}}>
            <h3>{header}</h3>
            <p>{text}</p>
        </div>
    );
}

export default ErrorModal;