import React, {useContext} from 'react';
import AuthContext from '../../context/authContext';

import classes from './styles/SignUp.module.css';

function SignUp({ newUser, inputChangeHandler}) {
   const {name, username, email, password} = newUser
   const authContext = useContext(AuthContext);

  return (
    <form method="submit" className={classes.SignUp} id='signUp'>
      <h2>Sign Up</h2>
      <div className={classes.form__field}>
        <label htmlFor="name">Name</label>
        <input
          type="text"
          value={name}
          name="name"
          placeholder="example: John Doe"
          onChange = {(e)=>inputChangeHandler(e)}
        />
      </div>
      <div className={classes.form__field}>
        <label htmlFor="email">Email</label>
        <input
          type="email"
          value={email}
          name="email"
          placeholder="example: jdoe1990@example.com"
          onChange = {(e)=>inputChangeHandler(e)}
        />
      </div>
      <div className={classes.form__field}>
        <label htmlFor="username">Username</label>
        <input
          type="username"
          value={username}
          name="username"
          placeholder="example: jdoe123"
          onChange = {(e)=>inputChangeHandler(e)}
        />
      </div>
      <div className={classes.form__field}>
        <label htmlFor="password">Password</label>
        <input
          type="password"
          value={password}
          name="password"
          placeholder="example: qwerty12345"
          onChange = {(e)=>inputChangeHandler(e)}
        />
      </div>
      <button type="submit" onClick={(e)=>authContext.signUpHandler(e)}>Sign Up</button>
    </form>
  );
}

export default SignUp;
